import React from "react";
import "./Footer.css";

import FacebookLogo from "../../images/logos/Facebook.png";
import InstagramLogo from "../../images/logos/Instagram.png";
import RedditLogo from "../../images/logos/Reddit.ico";
import GithubLogo from "../../images/logos/Github.png";

export default function Footer() {
   const items = ["Facebook", "Reddit", "Instagram", "Github"];
   const urls = [
      "https://www.facebook.com/vled3/",
      "https://www.reddit.com/user/Ciorap88/",
      "https://www.instagram.com/vladd.88/",
      "https://github.com/Ciorap88",
   ];
   const srcs = [FacebookLogo, RedditLogo, InstagramLogo, GithubLogo];

   return (
      <footer>
         <ul className="social-media">
            {items.map((item, index) => (
               <li key={item}>
                  <a
                     href={urls[index]}
                     target="_blank"
                     rel="noopener noreferrer"
                  >
                     <img
                        className="social-media-icon"
                        src={srcs[index]}
                        alt={item}
                     />
                  </a>
               </li>
            ))}
         </ul>
         <h2 className="email">Email: vlad.014916@gmail.com </h2>
         <h2 className="copyright">Â© 2020 Vlad Ciocoiu</h2>
      </footer>
   );
}
