import React from "react";
import "./Projects.css";

import SectionTitle from "../SectionTitle/SectionTitle";
import ListProject from "./ListProject/ListProject.jsx";

import SudokuImg from "../../images/projects/sudokuBig.png";
import PathfindingImg from "../../images/projects/pathfindingBig.png";

export default function Projects() {
   return (
      <div className="projects">
         <SectionTitle name="projects" left={false} />
         <ListProject
            img={SudokuImg}
            url="https://ciorap88.github.io/Sudoku-Solver/"
            title="Sudoku Solver"
            number="0"
         />
         <ListProject
            img={PathfindingImg}
            url="https://ciorap88.github.io/pathfinding-visualizer/"
            title="Pathfinding Visualizer"
            number="1"
         />
      </div>
   );
}
